footer {
    .footer_links {
        ul {
            display: flex;
            list-style: none;
            justify-content: flex-end;

            @media only screen and (max-width: 760px) {
                flex-direction: column;
            }

            li {
                margin-left: rem(17);

                @media only screen and (max-width: 760px) {
                    margin-left: 0;
                }

                &:not(:last-child) {
                    @media only screen and (max-width: 760px) {
                        margin-bottom: rem(15);
                    }
                }

                &:first-child {
                    @media only screen and (max-width: 1400px) {
                        margin-left: 0;
                    }
                }

                a {
                    color: black;
                    font-size: rem(15);
                    text-decoration: none;
                    text-underline-offset: 5px;

                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }
    }

}


ul.address {
    list-style: none;

    li {
        margin-bottom: rem(3);
        font-size: rem(18);
        font-weight: 300;

        &:last-child {
            margin-bottom: rem(25);
        }
    }
}